import React, { Component } from 'react';
import { Card, CardText } from 'reactstrap';

class About extends Component {
  render() {
    return (
      <div>
        <h3 className="text-center">About Us</h3>
        <Card body outline color="success" className="mb-3">
          <CardText>
            <p>This is just the about page of a simple app built with react, reactstrap and react-router-dom.</p>
            <p>This is also deployed to github page with gh-page script using <code>npm i gh-pages</code> or <code>yarn add gh-pages</code></p>
          </CardText>
        </Card>
      </div>
    )
  }
}

export default About;