import React, { Component } from 'react';

class Header extends Component {

    hStyle = () => {
        return {
            background: "grey",
            color:'white',
            margin:0,
            padding: 10
        }
    }
    render() {
        return (
            <div style={this.hStyle()}  className="text-center p-5 mb-3 bg-dark">
                <h1>Hello React</h1>
            </div>
        )
    }
}

export default Header;